import React, { useState } from 'react';
import './styles/search.css';
import Sidebar from './components/Sidebar.js';
import apiRequest from './components/Requests.js';

const SearchPage = () => {
  const [query, setQuery] = useState('');
  const [activeTab, setActiveTab] = useState('videos'); // 'videos' = Polls, 'users' = People
  const [isLoading, setIsLoading] = useState(false);
  const [results, setResults] = useState([]);
  const [hasSearched, setHasSearched] = useState(false);

  const handleSearch = async () => {
    if (!query.trim()) return;

    setIsLoading(true);
    setHasSearched(true);

    try {
      let response;
      if (activeTab === 'videos') {
        response = await apiRequest({
          url: `https://api.vote.vickz.ru/api/v2/find-poll/${encodeURIComponent(query)}`,
          method: 'GET',
          auth: true,
        });
      } else {
        response = await apiRequest({
          url: `https://api.vote.vickz.ru/api/v2/find-user-by-username`,
          method: 'GET',
          params: { username: query },
          auth: true,
        });
      }

      if (!response.ok) throw new Error('Request failed');

      const data = await response.json();
      setResults(Array.isArray(data) ? data : [data]);
    } catch (error) {
      console.error('Search failed:', error);
      setResults([]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleTabSwitch = (tab) => {
    if (tab === activeTab) return;
    setActiveTab(tab);
    setResults([]);
    setHasSearched(false);
  };

  return (
    <div className="search-page">
      <Sidebar />
      <div className="search-container">

        {/* Поисковая строка */}
        <div className="search-bar">
          <input
            type="text"
            placeholder="Enter query..."
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            onKeyDown={(e) => e.key === 'Enter' && handleSearch()}
          />
          <button onClick={handleSearch}>Search</button>
        </div>

        {/* Вкладки */}
        <div className="tabs">
          <button
            className={activeTab === 'videos' ? 'active' : ''}
            onClick={() => handleTabSwitch('videos')}
          >
            Polls
          </button>
          <button
            className={activeTab === 'users' ? 'active' : ''}
            onClick={() => handleTabSwitch('users')}
          >
            People
          </button>
        </div>

        {/* Загрузка */}
        {isLoading && <div className="loader">Loading...</div>}

        {/* Результаты поиска */}
        {!isLoading && results.length > 0 && (
          <>
            {activeTab === 'videos' && (
              <div className="videos-grid">
                {results.map((poll, index) => {
                  const handleCardClick = () => {
                    const username = localStorage.getItem("username");
                    const url = poll.user_username === username
                      ? `/#/poll-view-creator/${poll.id}`
                      : `/#/poll/${poll.id}`;
                    window.location.href = url;
                  };

                  return (
                    <div
                      key={index}
                      className="vote-card"
                      style={{ cursor: 'pointer' }}
                      onClick={handleCardClick}
                    >
                      <div className="vote-header">
                        <img
                          className="vote-avatar"
                          src={`https://api.vote.vickz.ru/api/v2/get-profile-picture/${poll.user_id}`}
                          alt="Avatar"
                        />
                        <span className={`vote-status ${poll.closed ? 'closed' : 'open'}`}>
                          {poll.closed ? 'Closed' : 'Open'}
                        </span>
                      </div>
                      <div className="vote-body">
                        <h3>{poll.name}</h3>
                        <p>{poll.description || 'No description'}</p>
                        <p className="poll-dates">
                          <span>Start: {new Date(poll.start_date).toLocaleDateString()}</span><br />
                          <span>End: {new Date(poll.end_date).toLocaleDateString()}</span>
                        </p>
                      </div>
                    </div>
                  );
                })}
              </div>
            )}

            {activeTab === 'users' && (
              <div className="users-list">
                {results.map((user, index) => (
                  <div key={index} className="user-card">
                    <img
                      src={`https://api.vote.vickz.ru/api/v2/get-profile-picture/${user.id}`}
                      alt={user.username}
                    />
                    <span>{user.username}</span>
                  </div>
                ))}
              </div>
            )}
          </>
        )}

        {/* Нет результатов */}
        {!isLoading && results.length === 0 && hasSearched && (
          <div className="no-results">No results found</div>
        )}
      </div>
    </div>
  );
};

export default SearchPage;
